import Refresher from "./table/refresher";
import LayoutSwitcher from "./table/layout-switcher";
require('es6-object-assign').polyfill();
document.addEventListener('AC_Table_Ready', function (e) {
    var table = e.detail.table;
    table.Refresher = new Refresher(table);
    var switcher = document.querySelector('.layout-switcher');
    if (switcher) {
        var layout_switcher = new LayoutSwitcher(switcher);
        layout_switcher.place();
    }
});
